<template>
    <div class="service" v-loading="loading">
        <div class="service-content" v-if="orderList">
            <div class="list-main">
                <div class="list-msg">
                    <span class="inner-span">{{$L('订单编号')}}：{{ orderList.orderSn }}</span>
                    <span class="order-time">{{$L('下单时间')}}：{{ orderList.addTime }}</span>
                </div>
                <div class="list-head">
                    <div class="item item-two">{{$L('产品信息')}}</div>
                    <div class="item item-one">{{$L('价格')}}</div>
                    <div class="item item-one">{{$L('实付金额')}}</div>
                    <div class="item item-one">{{$L('订单状态')}}</div>
                </div>
                <div class="list-info">
                    <div class="item item-two">
                        <div class="info" v-for="(item,key) in orderList.getProductInfo" :key="key">
                            <div class="list-main-left">
                                <img :src="item.smallImg" alt="">
                            </div>
                            <div class="list-main-right">
                                <p class="orderInfo">{{item.chrName}}</p>
                                <p class="gray" v-html="item.goodsSpec"></p>
                                <p class="gray">×{{item.goodsNumber}}</p>
                            </div>
                        </div>
                    </div>
                    <div class="item item-one">
                        <div class="price" v-for="(i,key) in orderList.getProductInfo" :key="key">
                            ￥{{i.goodsPrice}}<br><span v-if="i.oldPrice - i.goodsPrice > 0">{{i.oldPrice}}</span>
                        </div>
                    </div>
                    <div class="item item-one">
                        <p class="red">￥{{ orderList.orderAmount }}</p>
                        <p class="gray">{{ orderList.payName }}</p>
                    </div>
                    <div class="item item-one">
                        <div class="unconfirm">
                            {{$L('订单已完成')}}
                        </div>
                    </div>
                </div>
            </div>
            <el-form class="service-reason">
                <p class="return"><span class="black">{{ $L('可退金额')}}：</span><span class="red">￥{{ orderList.orderAmount }}</span></p>
                <p class="warn gray">{{$L('*该产品仅退回产品金额，已使用的积分,优惠券和快递费用将不予退还')}}</p>
                <p class="reason">{{$L('退货/退款原因')}}</p>
                <el-input maxlength="500" :rows="7" type="textarea" resize="none" v-model="remarks" :placeholder="$L('请输入原因')" class="remarks"></el-input>
                <el-form-item class="img-item">

                </el-form-item>
                <ul class="reason_img">
                    <li class="load-img" v-for="(item,index) in showImgArr" :key="index">
                        <div class="upload-list-cover">
                            <span class="bbx-font bbx-icon-shanchu" @click="handleRemove(index)"></span>
                        </div>
                        <img class="img" :src="item" />
                    </li>
                    <li class="load-img up-load-img" v-if="showImgArr.length < maxNum" >
                        <span class="bbx-font bbx-icon-jiahao"></span>
                        <input type="file" name="CommentsPic" class="upload" multiple accept="image/png,image/jpeg,image/jpg" @change="upImgFunc"/>
                    </li>
                </ul>
                <p class="size">*{{ $L('图片大于2M将无法上传') }}</p>
                <div class="btns">
                    <el-button @click="backFunc">{{ $L('返回') }}</el-button>
                    <el-button type="primary" @click="submitFunc">{{ $L('申请提交') }}</el-button>
                </div>
            </el-form>
        </div>
    </div>
</template>

<script>
    export default {
        name: "orderService",
        data() {
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                remarks: '',
                showImgArr:[],
                maxNum: 4,
                uploadList: [],
                uploadImgArr:[],
                orderList: {},
                imgList:[],
            }
        },
        created () {
          this.getData();
        },
        methods:{
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            backFunc() {
                this.$router.push({name:'orderList',params:{type:'all'}});
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let id = _self.$route.params.id;
                _self.$api('applyRefund', {orderId: id}, function (reData) {
                    if (reData.flag === 1) {
                        _self.orderList = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name:'orderList',params:{type:'all'}});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name:'orderList',params:{type:'all'}});
                    });
                });
            },
            submitFunc(){
                const _self = this;
                _self.loading = true;
                if(_self.remarks !== ''){
                    let id = _self.orderList.orderId;
                    _self.imgList[id] = {
                        'fileList':_self.uploadList
                    };
                    let arr = {
                        orderId:id,
                        refundResult:_self.remarks,
                        'choose_img': {
                            fileList: _self.imgList
                        }
                    };

                    //这一步是执行再次申请退款
                    if (_self.orderList.orderStatus == 4) {
                        arr.againRefund = 1;
                        arr.refundId = _self.orderList.refundId;
                    }

                    let postData = _self.$objToFormData(arr);
                    _self.$api('addApplyRefund', postData, function (reData) {
                        if (reData.flag === 1) {
                            _self.getData();
                            _self.$message({
                                type: 'success',
                                message: _self.$L('提交成功!')
                            });
                            _self.$router.push({name:'orderServiceInfo',parmars:{orderId:id}});
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name:'orderList',params:{type:'all'}});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name:'orderList',params:{type:'all'}});
                        });
                    }, true);
                } else {
                    _self.$message({
                        type: 'error',
                        message: _self.$L('请填写退款申请原因!')
                    });
                    _self.loading = false;
                }
            },
            //上传图片
            upImgFunc(event) {
                const _self = this;
                let files = event.target.files;
                let oldLen = _self.uploadLen;
                let len = files.length+oldLen;
                if(len < _self.maxNum){
                    _self.$message({
                        showClose: true,
                        message: '最多可上传'+_self.maxNum+'张，您还可以上传'+(_self.maxNum - len)+'张',
                        type: 'success'
                    });
                }
                if(files.length <= _self.maxNum){
                    for (let i=0; i < files.length; i++) {
                        let imgUrl = null;
                        let size = Math.floor(files[i].size / 1024);
                        let type = files[i].type.substring(6);
                        if (size > 2*1024*1024) {
                            _self.$Message({
                                showClose: true,
                                message: '请选择2M以内的图片！',
                                type: 'warning'
                            });
                            return false;
                        }
                        if(!(type === 'png' || type === 'jpeg' || type === 'jpg')){
                            _self.$Message({
                                showClose: true,
                                message: '请选择png、jpeg、jpg的图片格式！',
                                type: 'warning'
                            });
                            return false;
                        }
                        _self.uploadList.push({
                            'file':files[i]
                        });
                        _self.uploadLen++;
                        let reader = new FileReader();
                        reader.readAsDataURL(files[i]);
                        if(Number(_self.showImgArr.length + files.length) <= _self.maxNum){
                            reader.onload = function(evt){
                                imgUrl = evt.target.result;
                                _self.showImgArr.push(imgUrl);
                            }
                        } else {
                            if(  _self.showImgArr.length <= _self.maxNum ){
                                _self.$message({
                                    showClose: true,
                                    message: '最多可上传'+_self.maxNum+'张',
                                    type: 'success'
                                });
                            }
                        }

                    }
                } else {
                    _self.$message({
                        showClose: true,
                        message: '最多可上传'+_self.maxNum+'张!',
                        type: 'success'
                    });
                }

            },
            //删除图片
            handleRemove(index){
                this.uploadList.splice(index, 1);
                this.showImgArr.splice(index, 1);
                this.uploadLen = this.showImgArr.length;
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib.scss";
    @import "../../../assets/icon/iconfont.css";
    .service{
        padding: 20px;
        margin: 10px 0;
        .service-head{
            height: 50px;
            z-index: 999;
            margin: 0 -20px;
            background-color: #fff;
            .left{
                display: inline-block;
                float: left;
                margin-left: 20px;
            }
        }
        .service-content {
            width: 100%;
            font-size: 14px;
            background-color: #fff;
            padding: 0px 20px 50px;
            .list-head{
                width: 100%;
                display: flex;
                justify-content: flex-start;
                border: 1px solid #dcdfe6;
                .item{
                    height: 50px;
                    line-height: 50px;
                    font-size: 14px;
                    text-align: center;
                    background: #fff;
                    color: #666;
                    font-weight: 700;
                    &.item-one{
                        width: 20%;
                    }
                    &.item-two{
                        width: 40%;
                        padding-left: 20px;
                        text-align: left;
                    }
                }
            }
            .list-main{
                background-color: #fff;
                margin-bottom: 20px;
                border-bottom: 1px solid #ccc;
                .list-msg{
                    background: #fff;
                    font-size: 14px;
                    .inner-span{
                        padding-right: 40px;
                        height: 50px;
                        line-height: 50px;
                    }
                    .order-time{
                        color: #999;
                    }
                }
                .list-info{
                    display: flex;
                    justify-content: flex-start;
                    background: #fff;
                    padding: 20px 0;
                    .item {
                        display: inline-block;
                        text-align: center;
                        vertical-align: top;
                        &.item-one{
                            width: 20%;
                            height: 100px;
                            font-size: 14px;
                            .price {
                                height: 100px;
                            }
                            .font-grey{
                                color: #999;
                                text-decoration: line-through;
                            }
                        }
                        &.item-two{
                            width: 40%;
                            text-align: left;
                        }
                        .info{
                            position: relative;
                            width: 100%;
                            overflow: hidden;
                            padding-bottom: 20px;
                            &:last-child{
                                padding-bottom: 0;
                            }
                            .list-main-left{
                                width: 100px;
                                height: 100px;
                                float: left;
                                img{
                                    width: 100px;
                                    height: 100px;
                                }
                            }
                            .list-main-right{
                                padding-left: 18px;
                                float: left;
                                height: 100px;
                                width: calc(100% - 120px);
                                p{
                                    line-height: 26px;
                                }
                                .orderInfo{
                                    cursor: pointer;
                                    white-space: nowrap;
                                    text-overflow: ellipsis;
                                    overflow: hidden;
                                    word-break: break-all;
                                    padding: 10px 0;
                                    font-size: 14px;
                                    @extend .text-one-line;
                                }
                            }

                        }

                    }
                }

            }
            .service-reason{
                .return{
                    font-size: 18px;
                }
                .warn{
                    margin: 10px 0;
                }
                .reason{
                    font-size: 16px;
                    line-height: 50px;
                    background-color: #F2F2F2;
                    padding-left: 20px;
                }
                .remarks{
                    margin-top: 20px;
                    font-size: 14px;
                }
                .img-item{
                    margin-top: 20px;
                    margin-bottom: 0;
                    .img-input{
                        width: 100px;
                        height: 100px;
                        position: absolute;
                        opacity: 0;
                        left: 0;
                        top: 0;
                        z-index: 9999;
                        cursor: pointer;
                    }
                    .up-show{
                        height: 100px;
                        width: 100px;
                        border: 1px solid #DBDBDB;
                    }
                }
            }
            .reason_img {
                overflow: hidden;
                .load-img{
                    width: 200px;
                    height: 200px;
                    line-height: 200px;
                    float: left;
                    text-align: center;
                    position: relative;
                    overflow: hidden;
                    margin-right: 20px;
                    &.up-load-img{
                        background-color: #F0F1F5;
                        text-align: center;
                        margin-right: 15px;
                    }
                    &:hover .upload-list-cover{
                        display: block;
                    }
                    .img{
                        width: 100%;
                        height: auto;
                    }
                    .upload-list-cover{
                        display: none;
                        position: absolute;
                        top: 0;
                        bottom: 0;
                        left: 0;
                        right: 0;
                        background: rgba(0,0,0,.6);
                        .bbx-font{
                            color: #fff;
                            font-size: 16px;
                            cursor: pointer;
                        }
                    }
                    .upload{
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        opacity: 0;
                        cursor: pointer;
                    }
                }
            }

            .size{
                padding: 10px 0 40px;
            }
            .btns{
                border-top: 1px solid #ccc;
                padding: 40px 0;
                text-align: right;
                .el-button{
                    width: 100px;
                    height: 38px;
                    line-height: 38px;
                    padding: 0;
                }
                /deep/ .el-button+.el-button{
                    margin-left: 20px;
                }
            }
            .red{
                color: #FF0000;
            }
            .black{
                color: #333;
                font-weight: 700;
            }
            .gray{
                color: #999999;
            }
        }
    }
</style>